.carousel {
    position: relative;
}

.carousel_card {
    height: 100px;
    width: 180px;
    background-color: teal;
    font-family: courier, monospace;
    color: white;
    font-weight: bold;
    display: none;
    justify-content: center;
    align-items: center;
    text-align:center;
}

.carousel_card--selected {
    display: block;
}

.carousel {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    
}
.carousel_nav {
    width: 100%;
    padding: 20px 0;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
}

.carousel_btn {
    width: 10px;
    height: 10px;
    display: inline-block;
    background: rgba(255, 255, 255, 0.38);
    border-radius: 50%; 
    margin: 0 5px;
    cursor: pointer;
}

.carousel_btn--selected {
    background: rgba(255, 255, 255, 0.742);
}
